/** Main settings **/
.App {
  text-align: center;
  background-color: #222;
  width: 100%;
}

.App-header-container {
  height: 10%;
  width: inherit;
  z-index: 99;
  padding-bottom: 5px;
}

.App-header-middle-container {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  width: inherit;
  overflow: hidden;
}

.App-logo {
  height: 75px;
  vertical-align: middle;
  max-width: 75px;
}

.App-header {
  background-color: #222;
  height: 75px;
}

.App-title {
  display: inline-block;
  position: relative;
  font-size: 2em;
  font-family: 'Muli', sans-serif;
  color: #68b8ed;
  vertical-align: bottom;
  padding-left: 5px;
  vertical-align: middle;
}

/* Div for hero portraits */
.hero {
  display:inline-block;
  position:relative;
}

/* The image depicting the hero */
.hero-portrait {
  opacity: 0.5;
  width: 60px;
  height: 60px;
  margin: 3px;
  border-radius: 99px;
  -webkit-box-shadow: #B3B3B3 2px 2px 2px;
  box-shadow: #B3B3B3 2px 2px 2px;
}

.hero-portrait:hover {
  opacity: 1.0;
  cursor: pointer;
  -webkit-transition: visibility 0.2s linear,
  opacity 0.2s linear;
  -moz-transition: visibility 0.2s linear,
  opacity 0.2s linear;
  -o-transition: visibility 0.2s linear,
  opacity 0.2s linear; 
}

/* Selected hero image in hero-info page */
#hero-portrait-selected {
  opacity: 1.0;
  width: 75px;
  height: 75px;
  border-radius: 99px;
  border: 2px solid silver;
}

/** Tooltips **/
.hero span {
  position: absolute;
  opacity: 0.5;
  visibility: hidden;
  z-index: 1;
  cursor: pointer;
  background-color: #222;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  top: auto;
  max-width: 200px;
  padding: 5px;
  word-wrap: normal;
}

.hero:hover span {
  -webkit-transition: visibility 0.2s linear,
  opacity 0.2s linear;
  -moz-transition: visibility 0.2s linear,
  opacity 0.2s linear;
  -o-transition: visibility 0.2s linear,
  opacity 0.2s linear; 
  visibility: visible;
  opacity: 1;
  top: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.hero > span:hover {
  display: none;
}

/** END **/

.hero-container {
  background-color: #fff;
  width: 100%;
  padding-top:10px;
  padding-bottom: 50px;
}

.hero-info-container {
  margin-top: 15px;
  display: inline-block;
  width : 100%;
  text-align: center;
}

/* Container of the hero-stats */
.hero-info-middle-container{
  display: inline-block;
  border-left: 1px solid silver;
  border-right: 1px solid silver;
  border-radius: 5px;
  vertical-align: top;
  max-width:96%;
  max-height:100%;
  width: 440px;
  border-bottom: 4px solid silver;
  border-radius: 4px;
  background-color: #f9f9f9;
  margin: 1%;
}

/* Container for full hero attack art */
.hero-pose-art {
  margin: 1%;
  display: inline-block;
  width: 440px;
  max-width:96%;
  max-height:100%;
  padding-bottom: 30px;
}

.hero-full-atk-art, .hero-full-portrait-art, .hero-full-special-art, .hero-full-injured-art{
  width: 440px;
  max-width:100%;
  max-height:100%;
}

/* The name next to the hero portrait in hero-info of the selected hero */
.hero-name {
  font-size: 1.5em;
  border-bottom: 2px solid #3d4151;
  background-color: #333;
  color: #ededed;
  border-radius: 4px 4px 0px 0px;
  padding-top: 15px;
  padding-bottom: 5px;
}

/* Hero level container */
.hero-level {
  display: inline-block;
  width: 100%;
  margin-top: 10px;
  background-color: #ededed;
  border-top: 1px solid silver;
  border-bottom: 1px solid silver;
  padding: 5px 0px;
}

.level-content-container {
  display: inline-block;
  vertical-align: middle; 
}

/* Individual level selector container */
.level-1-container, .level-40-container {
  display: inline;
  padding: 5px;
  vertical-align: middle;
  white-space: nowrap;
}

/* Merge selector container */
.hero-merge-level {
  display:inline-block;
  position: relative;
  padding: 5px 0px;
  background-color: #ededed;
  border-top: 1px solid silver;
  border-bottom: 1px solid silver;
  width: 100%;
  margin-top: 10px;
}

/* Dropdown of the merge */
.merge-select-container {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
  width: 75px;
}

.hero-stat-attributes {
  display:inline-block;
  width: 100%;
  padding: 15px 0px;
}

/* Hp, Atk, Spd, Def and Res stats containers */
.hp-input, .atk-input, .spd-input, .def-input, .res-input {
  display: inline-block;
  width: 100%;
  padding: 10px 0px;
}

.hp-input label, .atk-input label, .spd-input label, .def-input label, .res-input label {
  vertical-align: middle;
  width: 20px;
  margin-left: 20%; /* push the boxes from right to left */
}

/* The stats input field container */
.hp-input-container, .atk-input-container, .spd-input-container, .def-input-container, .res-input-container {
  display: inline-block;
  vertical-align: middle;
  margin-right: 35%; /* push the boxes from right to left */
  float: right;
}

/* The actual input field */
#input-hp, #input-atk, #input-spd, #input-def, #input-res {
  height: 25px;
  width: 50px;
  padding-right: 4px;
  padding-left: 4px;
  display:inline-block;
  background-color: #fff;
  color: #666666;
  border-radius: 4px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-top-color: #e6e6e6;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  vertical-align: middle;
}

.iv-content-container{
  display: inline-block;
  width: 100%;
  font-weight: bold;
  background-color: #ededed;
  padding: 5px 0;
  border-top: 1px solid silver;
}

.boon-container {
  display: inline-block;
  margin-right: 10px;
}

.bane-container {
  display: inline-block;
  margin-left: 10px;
}

.boon {
  color: #16af84;
}

.bane {
  color : #bf2a2a;
}
/* Main hero image loading */
.image-load-enter {
  opacity: 0.01;
}

.image-load-enter.image-load-enter-active {
  opacity: 0.5;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.image-load-leave {
  opacity: 0.5;
}

.image-load-leave.image-load-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.image-load-appear {
  opacity: 0.01;
}

.image-load-appear.image-load-appear-active {
  opacity: 0.5;
  -webkit-transition: opacity .5s ease-in;
  -o-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}

/* Stat loading */
.stat-load-enter {
  opacity: 0.01;
}

.stat-load-enter.stat-load-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.stat-load-leave {
  opacity: 1;
}

.stat-load-leave.stat-load-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.stat-load-appear {
  opacity: 0.01;
}

.stat-load-appear.stat-load-appear-active {
  opacity: 1;
  -webkit-transition: opacity .5s ease-in;
  -o-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}

/* Some global settings */
img, .App-title, span {
  -o-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

li, ul {
  list-style-type: none;
}

label {
  cursor: text;
}

* { 
  box-sizing: border-box; 
  -moz-box-sizing: border-box; 
  -webkit-box-sizing: border-box; 
}

/* Search bar */
.search {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  z-index: 2;
  position: relative;
}

.react-autosuggest__container {
  display: inline-block;
  width: 100%;
  position: relative;
}

.react-autosuggest__input {
  width: 100%;
  display: inline-block;
  padding: 5px 10px;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  width: 100%;
  display: inline-block;
}

.react-autosuggest__suggestions-container--open {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 30px;
  padding-left: 5px;
  text-align:left;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 5px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}


